.App {
  /* width: 100%; */
  display: flex;
  /* align-items: center; */
  justify-content: space-between;
  /* flex-wrap: wrap; */
  padding: 1rem;
}

.input-container {
  border: 1px solid black;
  width: 49%;
  padding: 1rem;
}

.archive-container {
  border: 1px solid black;
  width: 49%;
  padding: 1rem;
}

@media only screen and (max-width: 40em) {
  .App {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .input-container,
  .archive-container {
    width: 100%;
    margin: 1rem 0;
  }
}
