.content {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
  padding: 0;
  margin: 0.5rem 0;
  background-color: white;
}

.content p {
  padding: 0 0.5rem;
  font-size: 1.2rem;
  margin: 0.3rem 0;
}

.content button {
  background-color: black;
  color: white;
  font-size: 1rem;
}
